<template>
    <div class="mainc">
        <div class="content-item">
            <div class="user-content">
                <img :src="userMsg.face" class="user-head">
                <div class="user-msg">
                    <div class="user-name">{{userMsg.nickname || ""}}</div>
                    <div class="user-mobile">{{userMsg.mobile || ""}}</div>
                </div>
                <span class="qricon" @click="predivImg" data-url='/uploads/store/code-50.jpg'></span>
            </div>
            <div class="personal-list">
                <router-link tag="div" class="personal-item order-all" :to="{path:'order1', query: { index: '1' }}">
                    全部订单
                </router-link>
                <router-link tag="div" class="personal-item order1" :to="{path:'order1', query: { index: 2 }}">未付款
                </router-link>
                <router-link tag="div" class="personal-item order2" :to="{path:'order1', query: { index: 3 }}">待提货
                </router-link>
                <router-link tag="div" class="personal-item order3" :to="{path:'order1', query: { index: 4 }}">已提货
                </router-link>

                <router-link tag="div" class="personal-item address" :to="{name:'address'}">我的自提点 <span
                        class="right-text">{{shopMsg.store_name}}</span></router-link>
                <router-link tag="div" class="personal-item discount" :to="{name:'coupon'}">我的优惠券</router-link>
            </div>
            <div class="btns-content">
                <button class="go-shopping" @click="backHome">去购物</button>
                <button class="quit-btn" @click="logOut">退出当前账号</button>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: "personal",
        data(){
            return {
                config: this.GLOBAL.config,
                qrcode: '',
                userMsg: {},
                shopMsg: {}
            }
        },
        created: function () {
            this.userMsg = this.GLOBAL.userMsg;
            this.shopMsg = this.GLOBAL.shopMsg;
        },
        mounted() {

        },

        methods: {
            //事件处理函数
            viewTap: function (e) {
                var urls = e.target.dataset.url;
                this.$router.push({name: urls});
            },
            //返回事件
            backHome: function () {
                localStorage.setItem('refresh', 1);
                this.$router.push({name: 'index'});
            },
            //预览图片 查看二维码
            predivImg: function (e) {
                var urls = this.config + e.target.dataset.url; //图片下标
                window.open(urls);
            },
            //退出当前账号
            logOut: function () {
                this.$createDialog({
                    type: 'confirm',
                    title: '退出登录',
                    content: '确认退出当前账号？',
                    onConfirm: () => {
                        this.GLOBAL.clear();
                        console.log(this.GLOBAL.userMsg,this.GLOBAL.shopMsg);
                        localStorage.removeItem('userMsg');
                        localStorage.removeItem('role');
                        localStorage.setItem('refresh', 1);
                        this.toast = this.$createToast({
                            txt: '已退出',
                            time: 1000,
                            type: 'txt'
                        })
                        this.toast.show()
                        setTimeout(() => {
                            this.$router.push({name: 'index'});
                        }, 1000)
                    },
                    onCancel: () => {
                        console.log('点击了取消');
                    }
                }).show()
            }
        },

        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_personal.css);
</style>