<template>
    <div>
        <cube-button @click="showAlert">Dialog - alert</cube-button>
    </div>
</template>
<script>
    export default {
        methods: {
            showAlert() {
                this.$createDialog({
                    type: 'confirm',
                    title: '退出登录',
                    content: '确认退出当前账号？'
                }).show()
            }
        }
    }
</script>